.hero {
  background-image: url(https://image.shutterstock.com/image-photo/businessman-suit-cape-hero-stand-260nw-1035824698.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 220px;

  grid-area: hero;
}
.profile {
  grid-template-columns: 4fr 8fr;
  grid-template-areas: 
  'hero hero'
  'info post_block'
  '. post_block';
  grid-gap: 20px;
}

.post_block {
  grid-area: post_block;
}