*{
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
a {
  text-decoration: none;
  color:#333;
  font-weight: 500;
}
.container {
  max-width: 1100px;
  width: 100%;
  margin: 0 auto;
}
.flex {
  display: flex;
}
.grid{
  display: grid;
}

.body-container {
  flex-direction: column;
}

.all_content {
  background-color: #fff;
}
.title {
  color: #333;
  font-size: 30px;
  font-weight: 500;
  padding-left: 10px;
  padding-bottom: 30px;
  border-bottom: 2px solid #f0f0f0;
}
