.avatar {
  width: 150px;
  height: 150px;
  background-image: url(https://images.unsplash.com/photo-1611915387288-fd8d2f5f928b?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxleHBsb3JlLWZlZWR8MXx8fGVufDB8fHx8&w=1000&q=80);
  background-size: cover;
  margin-right: 10px;
  border-radius: 50%;
  transition: .3s
}
.avatar:hover {
  transform: rotateZ(15deg);
}
.info_block {
  display: flex;
  flex-direction: row;
  margin-bottom: 20px;

  grid-area: info;
}
.info_text h3 {
  margin-top: 0;
}
.info_text h4 {
  margin: 5px 0 5px;
  padding: 0;
}